@use 'var';

.hero {
    padding: 5.6vw;
    min-height: 80vh;
    background-image: url(../images/hero_desktop.jpeg);
    text-align: center;
    background-size: cover;

    @media (max-width: 768px) {
        background-image: url(../images/hero_celular.jpeg);
        padding-top: calc(50vw + 25px);
        background-size: contain;
        background-repeat: no-repeat;
        margin-top: -55px;
    }

    .button {
        margin-top: 2vw;
        margin-bottom: 8px;   
    }
    &__content {
        max-width: 640px;

        &__branding {
            max-width: 320px;
            margin: 0 auto 38px;

            @media (max-width: 768px) {
                max-width: 60%;
            }
        }
        .title--small {
            margin-bottom: 64px;
        }
        p {
            margin-bottom: 20px;
            &:last-child {
                margin-top: 64px;
            }
        }
        

        &__combos {
            display: flex;
            justify-content: space-between;

            @media (max-width: 768px) {
                display: block;
                

            }
            &__combo {
                width: calc(50% - 12px);
                @media (max-width: 768px) {
                    width: 100%;
                    margin-bottom: 24px;
                    
                }

                
                img {
                    max-height: 60px;
                }
                
            }
        }
    }

}